.switches-1 {
	$green: rgba(19, 191, 17, 1);
	$heightSwitch: 30px;

	transition: .25s ease-in-out;

	*,
	*:before,
	*:after {
		transition: inherit;
	}

	.checkbox-label {
		display: block;
		background: #fff;
		height: $heightSwitch;
		width: $heightSwitch * 2;
		border-radius: $heightSwitch;
		margin: 50px auto;
		position: relative;
		box-shadow: 0 0 0 2px #ccc;

		.on {
			display: block;
			position: absolute;
			z-index: 0;
			left: 0;
			opacity: 1;
			min-width: 300px;
			line-height: $heightSwitch;
			opacity: 0;
			color: $green;
		}

		.off {
			display: block;
			position: absolute;
			z-index: 0;
			right: $heightSwitch * 2 + 15px;
			text-align: right;
			opacity: 1;
			min-width: 300px;
			line-height: $heightSwitch;
			opacity: 1;
			color: tomato;
		}

		&:before {
			content: '';
			display: block;
			position: absolute;
			z-index: 1;
			top: 50%;
			left: 2px;
			transform: translateY(-50%);
			border-radius: $heightSwitch - 2px;
			height: $heightSwitch - 2px;
			width: $heightSwitch;
			background: tomato;
		}
	}

	.checkbox {
		position: absolute;
		left: -5000px;

		&:checked {
			+.checkbox-label {
				// background: $green;

				.on {
					left: $heightSwitch * 2 + 15px;
					opacity: 1;
				}

				.off {
					right: 0px;
					opacity: 0;
				}

				&:before {
					left: 30px;
					background: $green;
				}
			}
		}
	}
}